if (location.pathname.match("drinks/new")){
  // location.pathname
  // URL
  // .match

  // drinks/new 
  document.addEventListener("DOMContentLoaded",()=>{
    // addEventListener
    // 

    // documenthtml

    // DOMContentLoaded"
    // Web

    // 
    const inputElement = document.getElementById("drink_tag_tag_name")
    // tweets_tag_nameform_withid
    
    // form_with
    // + 

    // form_with@drink
    // @drinknew@drink = DrinkTag
    // drink_tag
    // tag_name
    // drink_tag_tag_name

    inputElement.addEventListener("keyup",()=>{
      // 
      // 

      const keyword = document.getElementById("drink_tag_tag_name").value;
      // 
      const XHR = new XMLHttpRequest();
      // XHLHttpRequest Ajax
      // HTTP
    
      // 
      XHR.open("GET",`search/?keyword=${keyword}`,true);
      // open
      //  HTTP
      //  
      //  ON/OFF

      // GET
      // ?
      // ?keyword${keyword}

      // queryhttp://sample.jp/?name=tanaka
      // URL
      // ?<>=<>
      // ?

      // 
      // drinks
      // 
      // 



      // drinks#search
      // 

      XHR.responseType = "json";
      // 
      // js
      // json
      XHR.send();
      // tag.js
      // 
      // 
      XHR.onload = () => {


        const searchResult = document.getElementById("search-result");
        // 1.,search-result
        searchResult.innerHTML = "";
        // 
        // 

        // innerHTML
        // 
        // 

        // 
        // search-result
        if (XHR.response){
          // keyup
          // 

          // tagName

          // 

          const tagName = XHR.response.keyword;
          // 
          // 
          // ,
  
          // 
          // response

          tagName.forEach((tag) => {
          // forEachrailssearch
          // 
          // 
          const childElement = document.createElement("div");
          // 2.
          // ,
         

          childElement.setAttribute("class", "child");
          childElement.setAttribute("id", tag.id);
          // divclass,id
          // forEachtag

          childElement.innerHTML = tag.tag_name;
          // <div>tagname</div> 
          // innerHTML 
          // 
          // 3.tagtag_name
          // childElement
           searchResult.appendChild(childElement);
          // htmlsearch-result
          // childElement

          // 
          
          const clickElement = document.getElementById(tag.id);
          // 

          // ,id = tag.iddivhtml
          // 
          clickElement.addEventListener("click",()=>{
            // clickElement
            document.getElementById("drink_tag_tag_name").value = clickElement.textContent;
            // form_withid  
            // .value
            // 

            // clickElement
            // .textContent

            // 
            // 
            clickElement.remove();
            // 
          });
          });
        };
      };
    });
  });
};